.card {
    background: #fff;
    border-radius: 2px;
    position: relative;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin-bottom: 20px;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
    background:black;
  }
  .card > .img-container > img {
    height: 200px;
    width: 300px;
  }
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  text-align: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
.title {
    width: 100%;
    text-align: center;
    color: black;
  }
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

.scoreSummary{
